import * as React from 'react'
import Box from '@mui/material/Box'
import ListItem from '@mui/material/ListItem'
import ListItemText from '@mui/material/ListItemText'
import { FixedSizeList } from 'react-window'
import { Chip, Divider, Slider, Stack, TextField } from '@mui/material'
import { getData, updateData } from 'actions/localstorage/generalActions'

export default function Recyclerview(props) {
  let { data, hundle } = props
  const [element, setElement] = React.useState(null)
  const [value, setValue] = React.useState()

  React.useEffect(() => {
    if (element == null) return
    const [i, j] = element
    setValue(data[i].prices[j].price)
  }, [element])

  React.useEffect(() => {
    if (element == null || value == null) return
    hundle(element, value)
  }, [value])

  function getOrdre() {
    if (element == null) return 10
    const [i, j] = element
    const value = data[i].prices[j].price
    if (value == null) return 10
    return 2 * value
  }
  return (
    <div>
      <Stack
        style={{ visibility: element == null ? `hidden` : `visible` }}
        sx={{ height: 300, position: `fixed`, right: 0, bottom: 100 }}
        spacing={1}
        direction="row"
      >
        <Slider
          aria-label="Small steps"
          value={value ?? 1}
          onChange={(_, newValue) => setValue(newValue)}
          getAriaValueText={(value) => `${value}`}
          step={0.1}
          marks
          min={0}
          max={10}
          valueLabelDisplay="auto"
          orientation="vertical"
        />
      </Stack>

      <div
        style={{ visibility: element == null ? `hidden` : `visible` }}
        className="center"
      >
        <TextField
          value={value ?? ``}
          onChange={(event) => setValue(event.target.value)}
          inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
        />
      </div>
      <Box
        sx={{
          width: '100%',
          height: 600,
          bgcolor: 'background.paper',
          p: 2,
          // border: `2px solid red`,
        }}
      >
        <FixedSizeList
          height={600}
          itemSize={120}
          itemCount={data.length}
          overscanCount={1}
        >
          {renderRow}
        </FixedSizeList>
      </Box>
    </div>
  )

  function renderRow(props) {
    const { index, style } = props
    const element = data[index]
    const { product, prices } = element
    return (
      <ListItem style={style} key={index} component="div" disablePadding>
        <div>
          <b>{product}</b>
          <br />
          {showPrices(prices, product)}
          <Divider variant="inset" sx={{ p: 2 }} />
        </div>
      </ListItem>
    )
  }

  function showPrices(prices, product) {
    const min = Math.min(...prices.map((e) => e.price).filter((e) => e))
    return prices.map((e, j) => {
      const i = getData().findIndex((e) => e.product === product)
      const selected = element?.[0] === i && element?.[1] === j
      return (
        <Chip
          key={j}
          avatar={null}
          label={
            <div>
              <b>{e.name}: </b> <span>{e.price}</span>
            </div>
          }
          sx={{ m: 0.4, border: selected ? `2px solid blue` : `` }}
          color={e.price === min ? `success` : `default`}
          onClick={(event) => {
            setElement(selected ? null : [i, j])
          }}
        />
      )
    })
  }
}
