import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import { useEffect, useState } from 'react'
import ProductsList from 'components/Recyclerview'
import {
  getCart,
  getData,
  updateCart,
  updateData,
} from 'actions/localstorage/generalActions'

export default function CartView() {
  const [indices, setIndices] = useState(getCart())
  const [data, setData] = useState(getData())

  const hundle = (coord, newValue) => {
    const [i, j] = coord
    const clone = [...data]
    clone[i].prices[j].price = newValue
    setData(clone)
    updateData(clone)
  }

  useEffect(() => {
    updateCart(indices)
  }, [indices])

  return (
    <div style={{ padding: 30 }}>
      <Autocomplete
        options={data.filter((e, i) => !indices.includes(i))}
        autoHighlight
        getOptionLabel={(option) => option.product}
        renderOption={(props, option) => (
          <Box
            component="li"
            sx={{ '& > img': { mr: 2, flexShrink: 0 } }}
            {...props}
          >
            {option.product}
          </Box>
        )}
        renderInput={(params) => (
          <TextField
            {...params}
            label="Choose a country"
            inputProps={{
              ...params.inputProps,
              autoComplete: 'new-password', // disable autocomplete and autofill
            }}
          />
        )}
        onChange={(event, newValue) => {
          const newIndex = data.findIndex((e) => e.product === newValue.product)
          setIndices([newIndex, ...indices])
        }}
        selectOnFocus
      />
      <ProductsList
        data={data.filter((e, i) => indices.includes(i))}
        hundle={hundle}
      />
    </div>
  )
}
