import { useEffect, useState } from 'react'
import ProductsList from 'components/Recyclerview'
import { getData, updateData } from 'actions/localstorage/generalActions'

export default function GlobalView() {
  const [data, setData] = useState(getData())

  const hundle = (coord, newValue) => {
    const [i, j] = coord
    const clone = [...data]
    clone[i].prices[j].price = newValue
    setData(clone)
    updateData(clone)
  }

  return (
    <div style={{ padding: 30 }}>
      <ProductsList data={data} hundle={hundle} />
    </div>
  )
}
